import CloseIcon from '../Icons/close.svg'

function SettingModal({ 
    temperature, topp, apikey,
    onChange,
    onClose
 }) {

    return (
        <div className='modal-background center'>
            <div className='setting-container undraggable column'>
                <div className='row' style={{marginBottom:'16px'}}>
                    <h2>설정</h2>
                    <div className='flex'/>
                    <span
                        className="material-symbols-outlined clickable"
                        onClick={(e)=>onClose()}
                    >
                        close
                    </span>
                </div>
                <div className='column section' style={{marginBottom:'24px'}}>
                    <p style={{marginBottom:'6px'}}>Gemini API키</p>
                    <input 
                        type='password'
                        className='full'
                        value={apikey}
                        onChange={(e)=>onChange({ apikey : e.target.value })}
                    ></input>
                </div>
                <div className='row section'>
                    <p className='flex'>온도</p>
                    <input
                        type='text'
                        className='small'
                        value={temperature}
                        onChange={(e)=>{
                            const value = e.target.value;
                            if (!isNaN(value)) {
                                onChange({ temperature : value })
                            }
                        }}
                        ></input>
                </div>
                <div className='row section'>
                    <p className='flex'>top-p</p>
                    <input
                        type='text'
                        className='small'
                        value={topp}
                        onChange={(e)=>{
                            const value = e.target.value;
                            if (!isNaN(value)) {
                                onChange({ topp : e.target.value })
                            }
                        }}
                    ></input>
                </div>
            </div>
        </div>
    )
}

  
export default SettingModal;